html, body, #react-container, #react-container > div {
  height: 100%; }

body {
  font-family: sans-serif;
  font-size: 100%;
  padding: 0;
  margin: 0; }

h1, h2, h3, h4, h5, h6,
th, header {
  font-family: sans-serif;
  font-size: 100%;
  padding: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: black; }
  a:hover {
    text-decoration: underline; }

div.page {
  display: flex;
  justify-content: space-between;
  height: 100%; }

section > div:not(.about-menu, .whoops-404),
section > section {
  margin: 1em; }

div.home {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }
  div.home h1 {
    font-size: 3em;
    color: slategray; }
  div.home > nav {
    background-color: slategray;
    padding: 1em;
    border-top: dashed .5em ghostwhite;
    border-bottom: dashed .5em ghostwhite;
    width: calc(100% - 2em);
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap; }
    div.home > nav a {
      font-size: 2em;
      color: ghostwhite;
      flex-basis: 200px; }

div.whoops-404 {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  display: flex;
  width: 100%;
  height: 100%;
  margin: 0;
  justify-content: center;
  align-items: center;
  background-color: darkred;
  color: ghostwhite;
  font-size: 1.5em; }
  div.whoops-404 a {
    color: white;
    text-decoration: underline;
    margin: 1em; }
    div.whoops-404 a:hover {
      color: yellow;
      text-decoration: none; }

