section.game {
  flex-grow: 1;
  margin: 1em;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

  .game-board {
    width: 50%;
    min-width: 50%;
    float: left;
  }
  .game-info {
    width: 25%;
    max-width: 25%;
    float: left;
  }
  .game-localstore {
    width: 25%;
    max-width: 25%;
    float: right;
    overflow: hidden;
  }
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
  }
  .board {
    font-size: 24px;
  }  
  .square:focus {
    outline: none;
  }
    
  .game {
    display: flex;
    flex-direction: row;
  }

